import * as GW from 'gw-utils';
export const NOTHING = 0;
export const FLOOR = 1;
export const DOOR = 2;
export const WALL = 3;
export const DEEP = 4;
export const SHALLOW = 5;
export const BRIDGE = 6;
export const UP_STAIRS = 7;
export const DOWN_STAIRS = 17;
export const IMPREGNABLE = 8;
export const TILEMAP = {
    [NOTHING]: 'NULL',
    [FLOOR]: 'FLOOR',
    [DOOR]: 'DOOR',
    [WALL]: 'WALL',
    [IMPREGNABLE]: 'IMPREGNABLE',
    [DEEP]: 'LAKE',
    [SHALLOW]: 'SHALLOW',
    [BRIDGE]: 'BRIDGE',
    [UP_STAIRS]: 'UP_STAIRS',
    [DOWN_STAIRS]: 'DOWN_STAIRS',
};
export const SEQ = [];
export function initSeqence(length) {
    SEQ.length = length;
    for (let i = 0; i < length; ++i) {
        SEQ[i] = i;
    }
    GW.random.shuffle(SEQ);
}
export function fillCostGrid(source, costGrid) {
    costGrid.update((_v, x, y) => source.isPassable(x, y) ? 1 : GW.path.OBSTRUCTION);
}
export class GridSite {
    constructor(grid) {
        this.grid = grid;
    }
    get width() {
        return this.grid.width;
    }
    get height() {
        return this.grid.height;
    }
    hasXY(x, y) {
        return this.grid.hasXY(x, y);
    }
    isBoundaryXY(x, y) {
        return this.grid.isBoundaryXY(x, y);
    }
    get(x, y) {
        return this.grid.get(x, y) || 0;
    }
    copy(other, offsetX = 0, offsetY = 0) {
        this.grid.forEach((_c, i, j) => {
            const otherX = i - offsetX;
            const otherY = j - offsetY;
            const v = other.get(otherX, otherY);
            if (!v)
                return;
            this.grid.set(i, j, v);
        });
    }
    isPassable(x, y) {
        return (this.isFloor(x, y) ||
            this.isDoor(x, y) ||
            this.isBridge(x, y) ||
            this.isStairs(x, y) ||
            this.isShallow(x, y));
    }
    isNothing(x, y) {
        const v = this.grid.get(x, y);
        return v === NOTHING;
    }
    isDiggable(x, y) {
        const v = this.grid.get(x, y);
        return v === NOTHING;
    }
    isFloor(x, y) {
        return this.grid.get(x, y) == FLOOR;
    }
    isDoor(x, y) {
        const v = this.grid.get(x, y);
        return v === DOOR;
    }
    isBridge(x, y) {
        const v = this.grid.get(x, y);
        return v === BRIDGE;
    }
    isWall(x, y) {
        const v = this.grid.get(x, y);
        return v === WALL || v === IMPREGNABLE;
    }
    isObstruction(x, y) {
        return this.isNothing(x, y) || this.isWall(x, y);
    }
    isStairs(x, y) {
        const v = this.grid.get(x, y);
        return v === UP_STAIRS || v === DOWN_STAIRS;
    }
    isDeep(x, y) {
        return this.grid.get(x, y) === DEEP;
    }
    isShallow(x, y) {
        return this.grid.get(x, y) === SHALLOW;
    }
    isAnyWater(x, y) {
        return this.isDeep(x, y) || this.isShallow(x, y);
    }
    isSet(x, y) {
        return (this.grid.get(x, y) || 0) > 0;
    }
    setTile(x, y, tile) {
        if (this.grid.hasXY(x, y))
            this.grid[x][y] = tile;
    }
}
